header {
  position: relative;
  right: 0;
  left: 0;
  width: auto;
  height: 70px;
  padding: 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 4px 20px 16px 0px rgba(0, 0, 0, 0.1);
}

.nav_logo button {
  background: none;
  border: none;
  color: white;
  font-size: 30px;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
  transition: all 0.5s;
}
.nav_logo button:hover {
  transform: scale(1.1);
}
.pages_btn {
  display: flex;
  gap: 0 20px;
}

.pages_btn button {
  background: none;
  border: none;
  color: white;
  font-size: 30px;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
  transition: all 0.5s;
}

.pages_btn button:hover {
  background-color: var(--secondary-color);
  transform: scale(1.1);
}

.cv_btn button {
  background: none;
  border: none;
  color: white;
  font-size: 20px;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
  transition: all 0.5s;
}

.cv_btn button:hover {
  background-color: var(--secondary-color);
  transform: scale(1.1);
}

.menu_btn {
  font-size: 40px;
  cursor: pointer;
  transition: all 0.5s;
  position: absolute;
  right: 20px;
  bottom: -60px;
  display: none;
}

.menu_btn:hover {
  transform: scale(1.1);
}

.menu_btn.active {
  transform: rotate(90deg);
}

.dynamic_pages_btn {
  position: absolute;
  background: var(--secondary-color);
  padding: 20px;
  bottom: -230px;
  right: 20px;
  z-index: 20;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  justify-content: space-around;
  height: 120px;
  visibility: hidden;
}
.dynamic_pages_btn button {
  background: none;
  border: none;
  color: white;
  font-size: 20px;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
  transition: all 0.5s;
}
@media (max-width: 1024px) {
  .pages_btn {
    display: none;
  }
  .menu_btn {
    display: block;
  }
  .dynamic_pages_btn {
    visibility: visible;
  }
}
@media (max-width: 480px) {
  .nav_logo button {
    font-size: 20px;
  }
  .cv_btn button {
    font-size: 15px;
  }
}
