.home { 
    padding: 40px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    height: 670px;
}
    
.image_container img { 
    border-radius: 100%;
    width: 100%;
}
.image_container  {
    display: flex;
    width: 500px;
}
.info_container {
    max-width: 720px;
    width: 100%;
}

.info_container h1{ 
    font-size: 40px;
    letter-spacing: 2px;
}
.info_container p{ 
    font-size: 30px;
    line-height: 60px
}
span#fullname { 
    background: var(--secondary-color);
    padding: 5px;
    border-radius: 50px;
}