.project {
  padding: 40px;
  display: flex;
  gap: 60px;
  flex-direction: column;
}

.accordion_container {
  text-align: start;
}
.accordion_grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  gap: 20px;
}
.accordion {
  display: flex;
  flex-direction: column;
  width: 520px;
  position: relative;
}

.accordion button {
  display: flex;
  text-align: start;
  align-items: center;
  justify-content: space-between;
  font-size: 30px;
  background: var(--secondary-color);
  border: none;
  color: white;
  border-radius: 5px;
  padding: 5px 15px;
  cursor: pointer;
}

#arrow {
  font-size: 40px;
  line-height: 20px;
  transition: all 0.5s;
}
#arrow.active {
  transform: rotate(180deg);
}

.accordion_content {
  color: white;
  background: var(--secondary-color);
  border-bottom: 1px solid white;
  border-top: 1px solid white;
  padding: 10px;
  top: 55px;
  transition: all 0.3s;
  text-align: start;
}

.project_link {
  display: flex;
  gap: 20px;
  align-items: center;
}


@media (max-width : 480px) {
  .accordion button {
    font-size: 20px;
  }
}